@import "../../Styles/colors";

.welcome {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  animation: popup 0.3s linear;
  background-color: $darkblue;
  height: 100%;
}

@keyframes popup {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.arrow {
  margin-right: 5px;
  color: $teal;
}

.introduction {
  margin: 0 25px;
}

.single-tool {
  display: flex;
  flex-direction: row;
  padding: 5px;
}

.tool-container {
  display: grid;
  grid-template-columns: 33% 33% 33%;
}

.tools {
  width: 50%;
  margin: 0 25px;
}

