@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{height:100vh;width:100vw;background-color:#22344e;overflow-x:hidden !important;display:flex;flex-direction:column}.rain{position:absolute;left:0;width:100%;height:100%}.rain.back-row{z-index:1;bottom:60px;opacity:.5}.drop{background:linear-gradient(0deg, #aaa8a8 35%, rgba(0, 212, 255, 0) 15%);position:absolute;bottom:100%;width:3px;height:30px;z-index:10}@keyframes drop{0%{transform:translateY(0vh)}75%{opacity:1}100%{transform:translateY(90vh);opacity:0}}.splash{width:15px;height:10px;border-top:2px dotted rgba(255,255,255,.5);border-radius:50%;opacity:1;transform:scale(0);position:absolute;top:90vh;z-index:10}@keyframes splat{0%{opacity:1;transform:scale(0)}80%{opacity:1;transform:scale(0)}95%{opacity:.5;transform:scale(1)}100%{opacity:0;transform:scale(1.5)}}.App-container{width:100vw;position:absolute;height:43vh;top:57vh;color:#ccd6f6}.seattle{font-size:20vw;position:absolute;bottom:33%;font-family:"Montserrat",sans-serif;transition:.5s}.based{font-size:4vw;position:absolute;bottom:43vh;z-index:10;background-color:#22344e;color:#fff;right:0;opacity:.8;padding:5px 10px}.top{height:50vh}
.welcome{display:flex;flex-direction:row;justify-content:space-between;animation:popup .3s linear;background-color:#060f1d;height:100%}@keyframes popup{0%{opacity:0}100%{opacity:1}}.arrow{margin-right:5px;color:teal}.introduction{margin:0 25px}.single-tool{display:flex;flex-direction:row;padding:5px}.tool-container{display:grid;grid-template-columns:33% 33% 33%}.tools{width:50%;margin:0 25px}
a{text-decoration:none;color:#fff}.contact-container{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;height:100%;animation:popup .3s linear;background-color:#060f1d}@keyframes popup{0%{opacity:0}100%{opacity:1}}.link-container{display:flex;flex-direction:row;justify-content:space-evenly;width:100%}.contact-link{border:2px solid #fff;padding:5px 10px;border-radius:5px;font-weight:100;transition:.5s;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}.contact-link:hover{background-color:#fff}.contact-link:hover>a{color:#0a192f !important}.contact-link:hover>img{-webkit-filter:contrast(0%) brightness(0%);filter:contrast(0%) brightness(0%)}a:hover{color:#0a192f !important}.get-in-touch{font-weight:900}.contact-icon{width:15px;-webkit-filter:contrast(0%) brightness(500%);filter:contrast(0%) brightness(500%);margin-right:5px}
.navbar-container{position:absolute;height:50px;width:100vw;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;z-index:9;color:#fff}.link{cursor:pointer;transition:1s;color:teal}.link-text{padding:5px 10px;border:2px solid teal;border-radius:5px;transition:.5s}.link-text:hover,.current{background-color:teal;color:#0a192f}
.portfolio-container>a{text-decoration:none;color:#fff;font-weight:900}.portfolio-container{display:grid;grid-template-columns:25% 75%;animation:popup .3s linear;background-color:#060f1d;height:100%}@keyframes popup{0%{opacity:0}100%{opacity:1}}.portfolio-desc{margin:25px}.portfolio-image{width:400px;border:2px solid teal}.portfolio-item{display:flex;flex-direction:row;margin-top:15px}.portfolio-links{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}.project-nav{padding:5px 10px;border:2px solid teal;margin:5px 0;width:150px;text-align:center;color:teal;border-radius:5px;cursor:pointer}.project-nav-title{padding:5px 10px;border:2px solid teal;margin:5px 0;width:150px;text-align:center;color:teal;border-radius:5px}.recent-projects{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;margin-left:25px}
